import { useEffect, RefObject } from 'react'

import { UseOutsideClickConfig } from './'

const useClickOutside = (
  ref: RefObject<HTMLElement | null>,
  callback: (evt: MouseEvent) => void,
  config?: UseOutsideClickConfig
): void => {
  const handleClick = (evt: MouseEvent): void => {
    const isElemExcluded = config?.excludedElemRefs?.some(
      (item) =>
        (item?.current === evt.target) || item?.current?.contains(evt.target as Node)) ?? false

    if (!isElemExcluded && ref.current != null && !ref.current.contains(evt.target as Node)) {
      callback(evt)
    }
  }

  useEffect(() => {
    document.addEventListener('click', handleClick, config)
    return () => {
      document.removeEventListener('click', handleClick, config)
    }
  })
}

export default useClickOutside
